import React from "react";
import Hamburger from "./Hamburger";

const Navbar = () => {
  return (
    <div className="navbar">
      <Hamburger className="hamburger" />
      <nav>
        <ul>
          <li>logowanie</li>
          <li>rejestracja</li>
        </ul>
      </nav>
      <style jsx>
        {`
          .navbar {
            display: flex;
            justify-content: flex-end;
          }

          nav {
            position: absolute;
            z-index: 20;
            right: 0;
            top: 0;
            padding: 30px;
            background: black;
            color: white;
            display: block;
          }

          ul {
            list-style-type: none;
          }
        `}
      </style>
    </div>
  );
};

export default Navbar;
